import generateUniqueNumber from "./generateUniqueNumber";

const convertCategory = (Category) => {
  let localCategory = [];

  Category.map((element) => {
    let localChildren = [];
    if (element.preCategory) {
      element.preCategory.map((childrenElement) => {
        localChildren.push({
          id: generateUniqueNumber(),

          value: childrenElement.address + generateUniqueNumber(),
          title: childrenElement.ru_name,
        });
      });
    }
    let localElement = {
      id: generateUniqueNumber(),
      disabled:true,
      value: element.address + generateUniqueNumber(),
      title: element.ru_name,
      children: localChildren,
    };
    localCategory.push(localElement);
  });
  return localCategory;
};
export default convertCategory;
