import React, { useEffect, useState } from "react";
import "./styles/DeleteProductPage.css";

import { useUrlRequests } from "../Mechanism/UrlRequests";
import { Button, ColorPicker, Modal } from "antd";

import DataEntry from "../Components/DataEntry";
import BackButton from "../Components/BackButton";

function DeleteMeta() {
  const [open, setOpen] = useState(false);

  const [MetaTags, setMetaTags] = useState([]);
  const [loading, setLoading] = useState(true);

  const closeModal = () => {
    setOpen(false);
  };

  const [search, setSearch] = useState("");

  const Url = useUrlRequests();

  const deleteProduct = async (index) => {
    setOpen(true);
    setLoading(true);

    await Url.deleteMeta(index, setLoading);
  };
  // eslint-disable-next-line
  const transferPage = async (address) => {
    Url.Navigation(address);
  };

  //   const changeColor = (colors) => {
  //     setColors(colors.colors);
  //   };
  useEffect(() => {
    const fetchData = async () => {
      await Url.getMetaTags(setMetaTags, setLoading);
    };
    fetchData();
  }, []);

  return (
    <div className="Frame-DeleteProduct">
      <Modal
        title={<h1>  </h1>}
        footer={
          <Button type="primary" onClick={closeModal}>
            
          </Button>
        }
        loading={loading}
        open={open}
        onCancel={() => setOpen(false)}
      >
        <h2>  </h2>
      </Modal>
      <h2>  </h2>
      <DataEntry placeholder="" text={search} setText={setSearch} />
      <div className="FramePreviewDPP">
        {MetaTags.map((meta) => {
          if (meta.address.toLowerCase().includes(search.toLowerCase())) {
            return (
              <div
                className="PreviewFrameEP"
                key={meta.address}
                onClick={() => deleteProduct(meta.address)}
              >
                <h4>{meta.address}</h4>
              </div>
            );
          }
        })}
      </div>
      <BackButton />

    </div>
  );
}

export default DeleteMeta;
