import React from "react";
import "./styles/DataEntryMultiLine.css";

const DataEntryMultiine = ({ placeholder = "", text, setText }) => {
  return (
    <div className="Frame-EntryDEML">
      <h3>{placeholder}</h3>
      <textarea
        multiple={true}
        className="DataEntryDEML"
        placeholder={` ${placeholder}: `}
        value={text}
        onChange={(e) => {
          setText(e.target.value);
        }}
      />
    </div>
  );
};

export default DataEntryMultiine;
