import React from "react";
import "./styles/DataEntry.css";

const DataEntry = ({ placeholder = "", text, setText }) => {
  return (
    <div className="Frame-EntryDE">
      <h3>{placeholder}</h3>
      <input
        className="DataEntryDE"
        placeholder={` ${placeholder}: `}
        value={text}
        onChange={(e) => {
          setText(e.target.value);
        }}
      />
    </div>
  );
};

export default DataEntry;
