import React from "react";
import { TreeSelect } from "antd";

import "./styles/ChangeColor.css";

const ChangeColor = ({ colors, value, setValue }) => {
  const onChange = (newValue) => {
    setValue(newValue);
  };
  return (
    <div className="FrameCColor">
      <h3></h3>
      <h5>
                ,   
      </h5>

      <TreeSelect
        value={value}
        onChange={onChange}
        treeData={colors}
        style={{ width: "300px" }}
        treeCheckable={true}
      />
    </div>
  );
};
export default ChangeColor;
