import React from "react";
import { TreeSelect } from "antd";
import "./styles/ChangeCategory.css"

const ChangeCategory = ({ Category, setValue, value }) => {
  const onChange = (newValue) => {
    setValue(newValue);
  };
  return (
    <div className="FrameCC">
      <h3></h3>
      <TreeSelect
        showSearch
        style={{
          width: "100%",
        }}
        value={value}
        dropdownStyle={{
          maxHeight: 400,
          overflow: "auto",
        }}
        placeholder="Please select"
        allowClear
        treeDefaultExpandAll
        onChange={onChange}
        treeData={Category}
      />
    </div>
  );
};
export default ChangeCategory;
