import React, { useEffect, useState } from "react";
import "./styles/CreateFormPage.css";

import { useUrlRequests } from "../Mechanism/UrlRequests";

import UploadImages from "../Components/UploadImages";
import DataEntry from "../Components/DataEntry";

import DataInteger from "../Components/DataInteger";
import BackButton from "../Components/BackButton";

function CreateForm() {
  const [load, setLoad] = useState(" ");
  const [ru_name, setRu_name] = useState("");
  const [en_name, setEn_name] = useState("");
  const [price, setPrice] = useState(0);
  const [image, setImage] = useState("");

  const Url = useUrlRequests();
  
  // eslint-disable-next-line
  const transferPage = async (address) => {
    Url.Navigation(address);
  };

  const deleteImage = (item) => {
    setImage("");
  };
  const UploadImage = (item) => {
    setImage(item);
  };
  const CreateForm = async () => {
    setLoad(" ")
    await Url.createForm(ru_name, en_name, price, image, setLoad);
  };
  useEffect(() => {
    const fetchData = async () => {
      //   await Url.getForms(setFormsWithConverted);
    };
    fetchData();
  }, []);

  return (
    <div className="Frame-CreateForm">
      <DataEntry
        placeholder="P  "
        text={ru_name}
        setText={setRu_name}
      />
      <DataEntry
        placeholder="  "
        text={en_name}
        setText={setEn_name}
      />
      <h2 style={{ textAlign: "center" }}>
            <br />
          (90px/120px)   3/4
      </h2>
      <UploadImages
        UploadPhoto={UploadImage}
        QuantityPhotos={0}
        deletePhoto={deleteImage}
        photos={image}
      />
      <DataInteger
        placeholder=" "
        value={price}
        setNumber={setPrice}
      />
      <div className="ButtonCF" onClick={CreateForm}>
        {load}
      </div>
      <BackButton />

    </div>
  );
}

export default CreateForm;
