import React, { useEffect, useState } from "react";
import "./styles/DeleteProductPage.css";

import { useUrlRequests } from "../Mechanism/UrlRequests";
import { Button, Modal } from "antd";

import DataEntry from "../Components/DataEntry";
import BackButton from "../Components/BackButton";

function DeleteForm() {
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(true);

  const closeModal = () => {
    setOpen(false);
  };

  const [Forms, setForms] = useState([]);
  const [search, setSearch] = useState("");

  const Url = useUrlRequests();

  const deleteProduct = async (index) => {
    setOpen(true);
    setLoading(true);

    await Url.deleteForm(index, setLoading);
  };
  // eslint-disable-next-line
  const transferPage = async (address) => {
    Url.Navigation(address);
  };

  const changeForm = (form) => {
    setForms(form.forms);
  };
  useEffect(() => {
    const fetchData = async () => {
      await Url.getForms(changeForm, setLoading);
    };
    fetchData();
  }, []);

  return (
    <div className="Frame-DeleteProduct">
      <Modal
        title={<h1> </h1>}
        footer={
          <Button type="primary" onClick={closeModal}>
            
          </Button>
        }
        loading={loading}
        open={open}
        onCancel={() => setOpen(false)}
      >
        <h2> </h2>
      </Modal>
      <h2> </h2>
      <DataEntry
        placeholder=" "
        text={search}
        setText={setSearch}
      />
      <div className="FramePreviewDPP">
        {Forms.map((form) => {
          console.log(form);

          if (
            form["en_name"].toLowerCase().includes(search.toLowerCase()) ||
            form["ru_name"].toLowerCase().includes(search.toLowerCase())
          ) {
            return (
              <div
                className="PreviewFrameDPP"
                key={form.id}
                onClick={() => deleteProduct(form.id)}
              >
                <img
                  className="PreviewImageDPP"
                  key={form.image}
                  src={form.image}
                  alt="Uploaded"
                />
                <h4>{form.ru_name}</h4>
              </div>
            );
          }
        })}
      </div>
      <BackButton />

    </div>
  );
}

export default DeleteForm;
