import React, { useEffect, useState } from "react";
import "./styles/DeleteProductPage.css";

import { useUrlRequests } from "../Mechanism/UrlRequests";
import { Button, ColorPicker, Modal } from "antd";

import DataEntry from "../Components/DataEntry";
import BackButton from "../Components/BackButton";

function DeleteColor() {
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(true);

  const closeModal = () => {
    setOpen(false);
  };

  const [Colors, setColors] = useState([]);
  const [search, setSearch] = useState("");

  const Url = useUrlRequests();

  const deleteProduct = async (index) => {
    setOpen(true);
    setLoading(true);

    await Url.deleteColor(index, setLoading);
  };
  // eslint-disable-next-line
  const transferPage = async (address) => {
    Url.Navigation(address);
  };

  const changeColor = (colors) => {
    setColors(colors.colors);
  };
  useEffect(() => {
    const fetchData = async () => {
      await Url.getColors(changeColor, setLoading);
    };
    fetchData();
  }, []);

  return (
    <div className="Frame-DeleteProduct">
      <Modal
        title={<h1> </h1>}
        footer={
          <Button type="primary" onClick={closeModal}>
            
          </Button>
        }
        loading={loading}
        open={open}
        onCancel={() => setOpen(false)}
      >
        <h2> </h2>
      </Modal>
      <h2> </h2>
      <DataEntry
        placeholder=" "
        text={search}
        setText={setSearch}
      />
      <div className="FramePreviewDPP">
        {Colors.map((color) => {
          console.log(color);

          if (
            color["en_name"].toLowerCase().includes(search.toLowerCase()) ||
            color["ru_name"].toLowerCase().includes(search.toLowerCase())
          ) {
            return (
              <div
                className="PreviewFrameDPP"
                key={color.id}
                onClick={() => deleteProduct(color.id)}
              >
                <ColorPicker
                  value={color.rgb}
                  format="hex"
                  size="large"
                  disabled={true}
                />
                <h4>{color.ru_name}</h4>
              </div>
            );
          }
        })}
      </div>
      <BackButton />

    </div>
  );
}

export default DeleteColor;
