import React, { useState } from "react";
import "./styles/UploadImages.css";

const UploadImages = ({ UploadPhoto, QuantityPhotos, deletePhoto, photos }) => {
  const [changePhoto, setChangePhoto] = useState(0);
  const onChange = (e) => {
    for (let index = 0; index < e.target.files.length; index++) {
      const file = e.target.files[index];
      if (file) {
        const reader = new FileReader();
        reader.onload = handleReaderLoaded;
        reader.onerror = (error) => {
          console.error("Error reading file: ", error);
        };
        reader.readAsBinaryString(file);
      }
    }
  };
  
  const handleReaderLoaded = (e) => {
    console.log("file uploaded 2: ", e);
    let binaryString = e.target.result;
    try {
      const base64String = btoa(binaryString);
      UploadPhoto(base64String);
    } catch (error) {
      console.error("Error encoding file to base64: ", error);
    }
  };
  
  return (
    <div className="Frame-UploadImages">
      {QuantityPhotos ? "Photos uploaded " + QuantityPhotos : ""}

      <br />
      {photos[0] != null ? (
        <img
          className="ImageUI"
          src={`data:image;base64,${
            Array.isArray(photos) ? photos[changePhoto] : photos
          }`}
          onClick={() => deletePhoto(changePhoto)}
          alt="Uploaded"
        />
      ) : (
        <div className="Frame-EmptyUI">Empty</div>
      )}
      <div className="listImageUI">
        {photos.length > 1 && Array.isArray(photos)
          ? photos.map((item, index) => {
              return (
                <img
                  className="PreviewImageUI"
                  key={item}
                  src={`data:image;base64,${item}`}
                  onClick={() => {
                    setChangePhoto(index);
                  }}
                  alt="Uploaded"
                />
              );
            })
          : ""}
      </div>
      <input
        type="file"
        name="image"
        multiple
        id="file"
        accept=".jpg, .jpeg, .png, .raw, .heif, .dng"
        onChange={onChange}
      />
    </div>
  );
};

export default UploadImages;
