import React from "react";
import "./styles/DataInteger.css";
import isNumber from "../Mechanism/isNumber";
const DataInteger = ({ placeholder = "", value, setNumber }) => {
  return (
    <div className="Frame-EntryDI">
      <h3>{placeholder}</h3>
      <input
        className="DataEntryDI"
        placeholder={` ${placeholder}: `}
        value={value}
        onChange={(e) => {
          if (isNumber(e.target.value)) {
            setNumber(e.target.value);
          }
        }}
      />
    </div>
  );
};

export default DataInteger;
