import React, { useEffect, useState } from "react";
import "./styles/DeleteProductPage.css";

import { useUrlRequests } from "../Mechanism/UrlRequests";
import { Button, Modal } from "antd";

import DataEntry from "../Components/DataEntry";
import BackButton from "../Components/BackButton";

function DeleteProductPage() {
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(true);

  const closeModal = () => {
    setOpen(false);
  };

  const [Products, setProducts] = useState([]);
  const [search, setSearch] = useState("");

  const Url = useUrlRequests();

  const deleteProduct = async (index) => {
    setOpen(true);
    setLoading(true);

    await Url.deleteProduct(index, setLoading);
  };
  const transferPage = async (address) => {
    Url.Navigation(address);
  };

  useEffect(() => {
    const fetchData = async () => {
      await Url.getProducts(setProducts, setLoading);
    };
    fetchData();
  }, []);

  return (
    <div className="Frame-DeleteProduct">
      <Modal
        title={<h1> </h1>}
        footer={
          <Button type="primary" onClick={closeModal}>
            
          </Button>
        }
        loading={loading}
        open={open}
        onCancel={() => setOpen(false)}
      >
        <h2> </h2>
      </Modal>
      <h2> </h2>
      <DataEntry
        placeholder=" "
        text={search}
        setText={setSearch}
      />
      <div className="FramePreviewDPP">
        {Products.map((Product) => {
          if (
            Product["en_name"].name
              .toLowerCase()
              .includes(search.toLowerCase()) ||
            Product["ru_name"].name.toLowerCase().includes(search.toLowerCase())
          ) {
            return (
              <div
                className="PreviewFrameDPP"
                key={Product.id}
                onClick={() => deleteProduct(Product.id)}
              >
                <img
                  className="PreviewImageDPP"
                  key={Product.images[0]}
                  src={Product.images[0]}
                  onClick={() => {}}
                  alt="Uploaded"
                />
                <h4>{Product.ru_name.name}</h4>
              </div>
            );
          }
        })}
      </div>
      <BackButton />

    </div>
  );
}

export default DeleteProductPage;
