import React, { useEffect, useState } from "react";
import "./styles/CreateProductPage.css";

import { useUrlRequests } from "../Mechanism/UrlRequests";

import UploadImages from "../Components/UploadImages";
import DataEntry from "../Components/DataEntry";
import DataEntryMultiine from "../Components/DataEntryMultiLine";
import DataInteger from "../Components/DataInteger";
import ChangeCategory from "../Components/ChangeCategory";
import isNumber from "../Mechanism/isNumber";
import convertCategory from "../Mechanism/convertedCategory";
import ChangeColor from "../Components/ChangeColor";
import convertColors from "../Mechanism/convertColors";
import ChangeForm from "../Components/ChangeForm";
import convertForms from "../Mechanism/convertForms";
import BackButton from "../Components/BackButton";

function CreateProduct() {
  const [load, setLoad] = useState(" ");

  const [unfilteredCategory, setUnfilteredCategory] = useState([]);
  const [Photos, setPhotos] = useState([]);

  const [UseCategory, setUseCategory] = useState();
  const [Category, setCategory] = useState([]);

  const [UseColor, setUseColor] = useState([]);
  const [colors, setColors] = useState([]);
  const [UseForm, setUseForm] = useState([]);
  const [forms, setForms] = useState([]);

  const [Price, setPrice] = useState(0);

  const [ru_nameName, setRu_nameName] = useState("");
  const [ru_nameDesc, setRu_nameDesc] = useState("");
  const [en_nameName, setEn_nameName] = useState("");
  const [en_nameDesc, setEn_nameDesc] = useState("");

  const Url = useUrlRequests();

  const UploadPhoto = (photo) => {
    setPhotos((data) => [...data, photo]);
  };

  const deletePhoto = (index) => {
    console.log(Photos, index);

    setPhotos(Photos.filter((p) => p !== Photos[index]));
  };
  // eslint-disable-next-line

  const transferPage = async (address) => {
    Url.Navigation(address);
  };

  const convertCategoryPlusIdToCategory = (category) => {
    let output = "";

    for (let char of category) {
      if (!isNumber(char)) {
        output += char;
      } else {
        setUseCategory(output);
        convertCategoryToFullCategory(output);
        break;
      }
    }
  };

  const convertCategoryToFullCategory = (CategoryName) => {
    let output = {};
    for (let parent of unfilteredCategory) {
      parent.preCategory.map((item) => {
        if (item.address === CategoryName) {
          output = item;
        }
      });
    }
    return output;
  };

  const setColorsWithConverted = (Colors) => {
    setColors(convertColors(Colors));
  };

  const setFormsWithConverted = (Forms) => {
    setForms(convertForms(Forms));
  };
  const setCategoryWithConverted = (Category) => {
    setCategory(convertCategory(Category));
  };

  const CreateProduct = async () => {
    setLoad(" ");
    await Url.createProduct(
      { name: ru_nameName, desc: ru_nameDesc },
      { name: en_nameName, desc: en_nameDesc },
      Photos,
      convertCategoryToFullCategory(UseCategory),
      Price,
      UseForm,
      UseColor,
      setLoad
    );
  };
  useEffect(() => {
    const fetchData = async () => {
      await Url.getCategories(setCategoryWithConverted, setUnfilteredCategory);
      await Url.getColors(setColorsWithConverted);
      await Url.getForms(setFormsWithConverted);
    };
    fetchData();
  }, []);

  return (
    <div className="Frame-CreateProduct">
      <UploadImages
        UploadPhoto={UploadPhoto}
        QuantityPhotos={Photos.length}
        deletePhoto={deletePhoto}
        photos={Photos}
      />
      <DataEntry
        placeholder="P  "
        text={ru_nameName}
        setText={setRu_nameName}
      />
      <DataEntryMultiine
        placeholder="P  "
        text={ru_nameDesc}
        setText={setRu_nameDesc}
      />
      <DataEntry
        placeholder="  "
        text={en_nameName}
        setText={setEn_nameName}
      />
      <DataEntryMultiine
        placeholder="  "
        text={en_nameDesc}
        setText={setEn_nameDesc}
      />
      <DataInteger placeholder="" value={Price} setNumber={setPrice} />
      <ChangeCategory
        Category={Category}
        value={UseCategory}
        setValue={convertCategoryPlusIdToCategory}
      />
      <ChangeColor colors={colors} value={UseColor} setValue={setUseColor} />
      <ChangeForm forms={forms} value={UseForm} setValue={setUseForm} />
      <div className="ButtonCPP" onClick={CreateProduct}>
        {load}
      </div>
      <BackButton />

    </div>
  );
}

export default CreateProduct;
