import React, { useState, useMemo, useEffect } from "react";
import "./styles/CreateColorPage.css";

import { useUrlRequests } from "../Mechanism/UrlRequests";
import DataEntry from "../Components/DataEntry";
import { Modal } from "antd";
import BackButton from "../Components/BackButton";

function EditMeta() {
  const [load, setLoad] = useState(" ");
  const [loading, setLoading] = useState(" ");
  const [open, setOpen] = useState(true);
  const [search, setSearch] = useState("");

  const [address, setAddress] = useState("");
  const [title, setTitle] = useState("");
  const [desc, setDesc] = useState("");
  const [keywords, setKeywords] = useState("");
  const [MetaTags, setMetaTags] = useState([]);

  const Url = useUrlRequests();
  const copyTextToClipboard = async (text) => {
    try {
      await navigator.clipboard.writeText(text);
      console.log("     !");
    } catch (err) {
      console.error(":", err);
    }
  };
  const closeModal = () => {
    setOpen(false);
  };

  // eslint-disable-next-line
  const transferPage = async (address) => {
    Url.Navigation(address);
  };
  const CreateMetatag = async () => {
    setLoad(" ");

    await Url.createMeta(address, title, desc, keywords, setLoad);
  };
  const changeProduct = async (_meta) => {
    setAddress(_meta.address);
    setTitle(_meta.title);
    setDesc(_meta.description);
    setKeywords(_meta.keywords);
    closeModal();
  };
  useEffect(() => {
    const fetchData = async () => {
      await Url.getMetaTags(setMetaTags, setLoading);
    };
    fetchData();
  }, []);

  return (
    <>
      <Modal
        width={750}
        title={<h1> Metatag</h1>}
        footer={<></>}
        loading={loading}
        open={open}
      >
        <DataEntry
          placeholder=" "
          text={search}
          setText={setSearch}
        />
        <div className="FramePreviewEP">
          {MetaTags.map((meta) => {
            if (meta.address.toLowerCase().includes(search.toLowerCase())) {
              return (
                <div
                  className="PreviewFrameEP"
                  key={meta.address}
                  onClick={() => changeProduct(meta)}
                >
                  <h4>{meta.address}</h4>
                </div>
              );
            }
          })}
        </div>
      </Modal>
      <div className="Frame-CreateColor">
        <DataEntry
          placeholder="A "
          text={address}
          setText={setAddress}
        />
        <h7>  </h7>
        <h5>
          <a
            style={{ cursor: "pointer", color: "blue" }}
            onClick={() => copyTextToClipboard("MainPage")}
          >
            MainPage
          </a>{" "}
          -  
        </h5>
        <h5>
          <a
            style={{ cursor: "pointer", color: "blue" }}
            onClick={() => copyTextToClipboard("BasketPage")}
          >
            BasketPage
          </a>{" "}
          - 
        </h5>{" "}
        <h5>
          <a
            style={{ cursor: "pointer", color: "blue" }}
            onClick={() => copyTextToClipboard("CategoryPage")}
          >
            CategoryPage
          </a>{" "}
          - 
        </h5>
        <h5>
          <a
            style={{ cursor: "pointer", color: "blue" }}
            onClick={() => copyTextToClipboard("PreCategoryPage")}
          >
            PreCategoryPage
          </a>{" "}
          {"- >"}
        </h5>
        <h5>
          <a
            style={{ cursor: "pointer", color: "blue" }}
            onClick={() => copyTextToClipboard("NewInPage")}
          >
            NewInPage
          </a>{" "}
          - 
        </h5>
        <h5>
          <a
            style={{ cursor: "pointer", color: "blue" }}
            onClick={() => copyTextToClipboard("ReviewsPage")}
          >
            ReviewsPage
          </a>{" "}
          - 
        </h5>
        <DataEntry placeholder="Title" text={title} setText={setTitle} />
        <DataEntry placeholder="Description" text={desc} setText={setDesc} />
        <DataEntry
          placeholder="Keywords"
          text={keywords}
          setText={setKeywords}
        />
        <div className="ButtonCC" onClick={CreateMetatag}>
          {load}{" "}
        </div>
      <BackButton />

      </div>
    </>
  );
}

export default EditMeta;
